import { IS_LOADING, IS_MOBILE } from '../types';

const initialState = {
	isLoading: true,
};

function appReducer(state = initialState, action) {
	switch (action.type) {
		case IS_LOADING:
			return {
				...state,
				isLoading: action.payload,
			};
		case IS_MOBILE:
			return {
				...state,
				isMobile: action.payload,
			};
		default:
			return state;
	}
}

export default appReducer;
