import { NavLink } from 'react-router-dom';
import './MobileNav.css';

function MobileNav() {
	return (
		<ul className="mobilenav">
			<li className="mobilenav__item">
				<NavLink
					to="/about"
					className={({ isActive }) =>
						'mobilenav__link' + (isActive ? ' active' : '')
					}
				>
					<span className="material-icons">info</span>
					<p className="mobilenav__label">About</p>
				</NavLink>
			</li>
			<li className="mobilenav__item">
				<NavLink
					to="/"
					className={({ isActive }) =>
						'mobilenav__link' + (isActive ? ' active' : '')
					}
				>
					<span className="material-icons">login</span>
					<p className="mobilenav__label">Login</p>
				</NavLink>
			</li>
			<li className="mobilenav__item">
				<NavLink
					to="/playlists"
					className={({ isActive }) =>
						'mobilenav__link' + (isActive ? ' active' : '')
					}
				>
					<span className="material-icons">add_circle</span>
					<p className="mobilenav__label">Generate</p>
				</NavLink>
			</li>
		</ul>
	);
}

export default MobileNav;
